* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.App {
  max-width: 700px;
  margin: auto;
}

#top {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 100px;
}

#nameTitle {
  font-size: 40px;
  color: #cc5500;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
}

#blog {
  font-size: 20px;
  /* color: #cc5500; */
  text-decoration: none;
}

#blog:visited {
  color: black;
}

a {
  cursor: pointer;
}

#navBar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.navTitle {
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
}

#profileTitle, #selectedProfileTitle {
  margin-left: 50px;
}

#selectedProfileTitle, #selectedProjectsTitle, #selectedResumeTitle {
  text-decoration: underline;
}

#projectsTitle, #selectedProjectsTitle {
  margin-left: 20px;
  margin-right: 20px;
}

#resumeTitle, #selectedResumeTitle {
  margin-right: 20px;
}

#youTube {
  font-size: 20px;
  /* color: #cc5500; */
  text-decoration: none;
  margin-left: 20px;
}

#youTube:visited {
  color: black;
}

/* #youTubeIcon {
  margin-right: 15px;
} */

.contactIcon {
  height: 35px;
  width: 35px;
}

#githubIcon {
  margin-right: 15px;
  margin-left: 15px;
}

#footer {
  margin-top: 40px;
  margin-bottom: 100px;
  text-align: center;
}

#footerTitle {
  font-size: 25px;
  font-family: Arial, Helvetica, sans-serif;
  color: #cc5500;
}

@media (max-width: 580px) {
  body {
    padding: 20px;
  }

  #top {
    margin-top: -100px;
  }

  #nameTitle {
    font-size: 40px;
  }

  #navBar {
    flex-direction: column;
    margin-left: 130px;
    margin-top: 130px;
  }

  #selectedProjectsTitle, #projectsTitle {
    margin-top: 20px;
    margin-bottom: 20px;
  }

  #selectedProfileTitle, #selectedProjectsTitle, #selectedResumeTitle, #profileTitle, #projectsTitle, #resumeTitle {
    margin-left: 0;
    margin-right: 0;
  }

  #blog {
    margin-top: 20px;
  }
}