#resumeContainer {
  display: flex;
  flex-direction: row;
}

#resumeLeft {
  width: 40%;
}

#resumeRight {
  width: 40%;
  padding-left: 100px;
}

.sideTitle {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 35px;
  margin-top: 0px;
  text-decoration: underline;
}

.school, .title {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}

.date {
  font-size: 20px;
}

.edStint p {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 150%;
}

/* .edStint {
  margin-bottom: 80px;
} */

.moreInfo {
  font-size: 18px;
}

#accomplishments {
  margin-top: 87px;
}

.moreInfoRight {
  font-size: 20px;
  line-height: 120%;
  font-family: Arial, Helvetica, sans-serif;
  width: 150%;
}

#experience {
  margin-bottom: 100px;
}

@media (max-width: 580px) {
  #resumeContainer {
    flex-direction: column;
  }
  
  #resumeRight {
    padding-left: 0px;
  }

  #resumeLeft {
    width: 60%;
  }
}